<template lang='pug'>
    div
        el-row(:gutter="10")
            el-col(:span='12')
                el-card.box-card
                    .clearfix(slot='header')
                        span 机票预定
                    el-form(:model='flight', label-position='left', label-width='80px')
                        el-form-item(label='行程')
                            //- el-row
                            el-col(:span='10')
                                cityCard(placeholder='输入国家/城市/机场', v-model='flight.depart', @itemSelect='departSelect')
                            el-col(:span='4', style='text-align: center')
                                el-button.exchange(round, size='small', @click='cityExchange') 换
                            el-col(:span='10')
                                cityCard(placeholder='输入国家/城市/机场', v-model='flight.arrive', @itemSelect='arriveSelect')
                        el-form-item(label='出发日期')
                            el-form-item
                                el-col(:span='8')
                                    el-date-picker(type='date', v-model='flight.setTime', placeholder='yyyy-mm-dd', style='width: 100%')
                        el-form-item
                            el-button(type='success', style='width: 100px', @click='flightSearch') 搜索
            el-col(:span='12')
                el-card.box-card
                    .clearfix(slot='header')
                        span 酒店预订
                    el-form(:model='hotel', label-position='left', label-width='80px')
                        el-form-item(label='目的地')
                            el-col(:span='8')
                                el-form-item
                                    el-input(v-model='hotel.destination', placeholder='北京')
                            el-col(:span='16')
                                el-form-item
                                    el-input(v-model='hotel.detail', placeholder='(选填)酒店名、地标、商圈，可组合搜索')
                        el-form-item(label='日期选择')
                            el-col
                                el-date-picker(v-model='hotel.rangeTime', type='daterange', range-separator='至', start-placeholder='入住日期', end-placeholder='离店日期')
                        el-form-item
                            el-button(type='primary', style='width: 100px') 搜索
        hr
        el-row(:gutter="10")
            el-col(:span='12')
                el-card.box-card
                    .clearfix(slot='header')
                        span 机票订单管理
                        .card-extra
                            el-input(style='width: 200px; margin-right: 10px; float: left', placeholder='输入条件', size='small')
                            el-button(type='danger', size='small') 订单搜索
                    el-table(:data='hotel_data')
                        el-table-column(prop='order_num', label='订单号')
                        el-table-column(prop='client_name', label='客户姓名')
                        el-table-column(prop='order_date', label='入住日期')
                        el-table-column(prop='hotel_name', label='酒店名称')
                        el-table-column(prop='order_name', label='下单人')
                        el-table-column(prop='status', label='状态')
            el-col(:span='12')
                el-card.box-card
                    .clearfix(slot='header')
                        span 酒店订单管理
                        .card-extra
                            el-input(style='width: 200px; margin-right: 10px; float: left', placeholder='输入条件', size='small')
                            el-button(type='danger', size='small') 订单搜索
                    el-table(:data='hotel_data')
                        el-table-column(prop='order_num', label='订单号')
                        el-table-column(prop='client_name', label='客户姓名')
                        el-table-column(prop='order_date', label='入住日期')
                        el-table-column(prop='hotel_name', label='酒店名称')
                        el-table-column(prop='order_name', label='下单人')
                        el-table-column(prop='status', label='状态')
</template>

<script>
import cityCard from '../common/CityCard.vue'
export default {
    components: {cityCard},
    data () {
        return {
            hotel: {
                destination: '',
                detail: '',
                rangeTime: ''
            },
            flight: {
                depart: '',
                arrive: '',
                setTime: '',
                theDepart: {},
                theArrive: {}
            },
            hotel_data: [
                {
                    order_num: 231,
                    client_name: '朱元璋',
                    order_date: new Date(),
                    hotel_name: '庆功楼',
                    order_name: '朱棣',
                    status: '待入住'
                },
                {
                    order_num: 231,
                    client_name: '朱元璋',
                    order_date: new Date(),
                    hotel_name: '庆功楼',
                    order_name: '朱棣',
                    status: '待入住'
                }
            ],
            value: ''
        }
    },
    methods: {
        pop () {
            console.log(event.type)
        },
        flightSearch () {
            if (this.flight.depart === '' || this.flight.arrive === '') {
                this.$Message.warning('查询条件为空')
            } else if (this.flight.depart === this.flight.arrive) {
                this.$Message.warning('到达城市不能与出发城市相同')
            } else {
                let time = this.flight.setTime.getFullYear() + '-' + (this.flight.setTime.getMonth() + 1) + '-' + this.flight.setTime.getDate()
                this.$router.push({path: '/flight', query: {depart: this.flight.theDepart, arrive: this.flight.theArrive, time: time}})
            }
        },
        departSelect (item) {
            this.flight.theDepart = item
        },
        arriveSelect (item) {
            this.flight.theArrive = item
        },
        cityExchange () {
            let a = ''
            let b = {}
            a = this.flight.depart
            this.flight.depart = this.flight.arrive
            this.flight.arrive = a
            b = this.flight.theDepart
            this.flight.theDepart = this.flight.theArrive
            this.flight.theArrive = b
        }
    },
    created () {
        let now = new Date()
        this.hotel.startTime = new Date()
        this.hotel.endTime = now.setDate(now.getDate() + 1)
        this.flight.setTime = new Date()
        this.flight.depart = '北京(BJS)'
        this.flight.arrive = '上海(SHA)'
        this.flight.theDepart = {
            airportCode: 'PEK',
            airportNameCN: '北京首都国际机场',
            airportNameEN: 'beijing',
            cityCode: 'BJS',
            cityNameCN: '北京',
            cityNameEN: 'beijing',
            cityPinyin: 'beijing',
            cityPy: 'bj',
            isHot: 'Y'
        }
        this.flight.theArrive = {
            airportCode: 'PVG',
            airportNameCN: '上海浦东机场',
            airportNameEN: 'Shanghai',
            cityCode: 'SHA',
            cityNameCN: '上海',
            cityNameEN: 'Shanghai',
            cityPinyin: 'shanghai',
            cityPy: 'sh',
            isHot: 'Y'
        }
    },
    mounted () {
        // setTimeout(function () {
        //     let ele = document.querySelectorAll('.ivu-card-extra')
        //     ele[0].style.top = '9px'
        //     ele[1].style.top = '9px'
        // }, 0)
    }
}
</script>

<style scoped>
    hr {
        margin: 10px 0;
    }
    .card-extra {
        float: right;
        margin-top: -5px;
    }
    .box-card {
        overflow: visible;
    }
    .exchange {
        padding: 6px;
        font-family: "幼圆";
        color: #352b2b;
        background-color: #f5eded;
    }
    .exchange:hover {
        color: #409eff;
        background-color: #fff;
    }
</style>



